import React from 'react';
import { Box, Typography, CardMedia } from '@mui/material';
import codeEditorsImage from '../assets/code-editors.jpg'; //   

const Home = () => {
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        minHeight: '100vh', //      
        textAlign: 'center',
        p: 3, // 
        marginLeft: 0, //    
      }}
    >
      {/*  */}
      <Box
        sx={{
          width: '100%',
          maxWidth: '800px', //   
          mb: 4, //  
        }}
      >
        <CardMedia
          component="img"
          image={codeEditorsImage}
          alt=" "
          sx={{
            width: '100%', //   100%   
            height: 'auto', //   
            borderRadius: 2,
            boxShadow: 3,
          }}
        />
      </Box>

      {/*  */}
      <Typography
        variant="h3"
        component="h1"
        sx={{
          fontWeight: 'bold',
          color: 'primary.main',
          mb: 2, //  
        }}
      >
         !
      </Typography>

      {/*  */}
      <Typography
        variant="h5"
        component="p"
        sx={{
          color: 'primary.main', //    
          lineHeight: 1.6,
          mb: 4, //  
        }}
      >
                      -.
      </Typography>

      {/*  */}
      <Box
        sx={{
          mt: 'auto', //    
          py: 3, //    
          width: '100%',
          backgroundColor: 'background.paper', //  
          textAlign: 'center',
        }}
      >
        <Typography variant="body1" color="text.secondary">
             
        </Typography>
        <Typography variant="body2" color="text.secondary">
           2025   
        </Typography>
      </Box>
    </Box>
  );
};

export default Home;