import React from 'react';
import { Container, Typography, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from '@mui/material';

const Editors = () => {
  const editors = [
    { name: 'VS Code', description: '  Microsoft', link: 'https://code.visualstudio.com/' },
    { name: 'Sublime Text', description: '   ', link: 'https://www.sublimetext.com/' },
    { name: 'Atom', description: '  GitHub', link: 'https://atom.io/' },
  ];

  return (
    <Container>
      <Typography variant="h4" gutterBottom>
          
      </Typography>
      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {editors.map((editor) => (
              <TableRow key={editor.name}>
                <TableCell>{editor.name}</TableCell>
                <TableCell>{editor.description}</TableCell>
                <TableCell>
                  <a href={editor.link} target="_blank" rel="noopener noreferrer">
                    
                  </a>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Container>
  );
};

export default Editors;