import React from 'react';
import { Container, Typography, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from '@mui/material';

const Plugins = () => {
  const plugins = [
    { name: 'ESLint', description: '  JavaScript', link: 'https://eslint.org/' },
    { name: 'Prettier', description: ' ', link: 'https://prettier.io/' },
    { name: 'Auto Rename Tag', description: '  ', link: 'https://marketplace.visualstudio.com/items?itemName=formulahendry.auto-rename-tag' },
  ];

  return (
    <Container>
      <Typography variant="h4" gutterBottom>
          -
      </Typography>
      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {plugins.map((plugin) => (
              <TableRow key={plugin.name}>
                <TableCell>{plugin.name}</TableCell>
                <TableCell>{plugin.description}</TableCell>
                <TableCell>
                  <a href={plugin.link} target="_blank" rel="noopener noreferrer">
                    
                  </a>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Container>
  );
};

export default Plugins;